        <div class="features__tooltip">
          <span class="features__tooltipCursor" :style="pos" ref="tooltipCursor"></span>
          <p class="features__text">{{ tooltipText }}</p>
        </div>

        <div v-for="(value, featureName, index) in bottomRowFeatures"
             :class="`feature-${featureName}`"
             :key="index"
             @mouseenter="changeTooltip(value, index, 'bottom')">
          <p class="feature__title">{{ value. title }}</p>
