          },
          certificate: {
            title: 'Поддержка DNS',
            tooltip: 'exercitationem laborum rem repellendus soluta. Ab accusamus hic illumiusto, officia quasi rerum! Ipsam, iusto!'
          },
        },
        bottomRowFeatures: {
          dnssec: {
            title: 'Поддержка DNS',
            tooltip: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Nihil quaerat, veniam.Animi architecto assumenda deserunt exercitationem laborum rem repellendus soluta. Ab accusamus hic illumiusto, officia quasi rerum! Ipsam, iusto!'
          },
