          },
        }
      }
    },
    methods: {
      changeTooltip(value, index, direction) {
        this.tooltipText = value.tooltip;
        this.pos = this.tooltipCursorPosition(index, direction);

      },
      tooltipCursorPosition(quantityOffset, direction) {
        const offset = (100 / 12 * 3 ) * quantityOffset; //не очень красиво получилось
        const middleCol = (100 / 12 * 3 ) / 2 ;

        const position = {};
        position.left = `${middleCol + offset}%`;
        position[direction] = '-20px';

        return position;
      }
    },
    created() {
      this.changeTooltip(this.topRowFeatures.dns, 0, 'top')
    }

  }
</script>

